	module.exports = {"data":{"site":{"siteMetadata":{"title":"Step Over","subtitle":"有趣的灵魂终会相遇","copyright":"© All rights reserved.","author":{"name":"周骅","rss":"#","email":"zhou--hua@163.com","github":"zhouhua-js"}}},"markdownRemark":{"id":"/work/playground/zhouhua.site/src/pages/articles/2014/percent.md absPath of file >>> MarkdownRemark","html":"<p>今天要说一个很基础，但被我自己忽略至今的一个简单问题：padding、margin在纵向取值为百分数时，它们相对的是父元素的高度吗？ 这个问题之所以被忽略，是因为使用场景有限。因为在布局的时候，我们通常只关注宽度，流式布局、响应式布局中，我们常常会给padding设置横向的百分比数值。但纵向上，我们通常还是倾向于设置固定值。回到问题本身，我一直以为在横向上参照父元素的宽度，那么在纵向上应该参照父元素的高度。这其实是不对的，都是参照父元素的宽度。验证很容易，我不做示例了，贴上MDN的资料：</p>\n<blockquote>\n<h3>Values</h3>\n<p>Specifies one, two, three or four of the following values:</p>\n<dl>\n<dt>&lt;length&gt;</dt>\n<dd>Specifies a non-negative fixed width. See [**&lt;length&gt;**](https://developer.mozilla.org/en-US/docs/Web/CSS/length) for details.</dd>\n<dt>&lt;percentage&gt;</dt>\n<dd>With respect to the** width **of the containing block.</dd>\n</dl>\n<ul>\n<li><strong>One</strong> single value applies to <em>all 4 sides</em></li>\n<li><strong>Two</strong> values apply to <strong>1.</strong> <em>top and bottom</em> and <strong>2.</strong> to the <em>left and right</em> side</li>\n<li><strong>Three</strong> values apply to <strong>1.</strong> <em>top</em>, <strong>2.</strong> <em>right and left</em> and <strong>3.</strong> to the <em>bottom</em> side</li>\n<li><strong>Four</strong> values apply to <strong>1.</strong> <em>top</em>, <strong>2.</strong> <em>right</em>, <strong>3.</strong> <em>bottom</em> and <strong>4.</strong> to the <em>left</em> side</li>\n</ul>\n</blockquote>\n<ul>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/padding\" title=\"MDN padding\">MDN padding</a></li>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/margin\" title=\"MDN margin\">MDN margin</a></li>\n</ul>","excerpt":"今天要说一个很基础，但被我自己忽略至今的一个简单问题：padding、margin在纵向取值为百分数时，它们相对的是父元素的高度吗？ 这个问题之所以被忽略，是因为使用场景有限。因为在布局的时候，我们通常只关注宽度，流式布局、响应式布局中，我们常常会给padding…","fields":{"tagSlugs":["/tags/css/","/tags/margin/","/tags/padding/"]},"frontmatter":{"title":"padding、margin纵向取值问题","tags":["CSS","margin","padding"],"date":"2014-12-23T23:30:53.000Z","description":null}}},"pathContext":{"slug":"/2014/percent/"}}

